import { Injectable } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { from, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { UIElementsService } from '../../shared/services/uielements.service';
import { AuthService } from '../services/auth.service';

@Injectable({
  providedIn: 'root'
})

export class AuthGuard implements CanActivate {
  constructor(private angularFireAuth: AngularFireAuth,
    private uiElementsService: UIElementsService,
    private authService: AuthService,
    private router: Router){

  }
   canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ){
    // const user = await this.angularFireAuth.currentUser;
    // const isLoggedIn = !!user;
    // if(!isLoggedIn){
    //   this.uiElementsService.addSnackBar('Must be logged in');
    //   console.log(isLoggedIn);
    //   window.setTimeout(() => {
    //     this.router.navigate(['/authentication'])
    //   }, 100)
    // }
    // return isLoggedIn;

    return from(this.authService.user$).pipe(
      map(authState => {
        if (authState) {
          this.authService.authSubject.next(true);
          return true;
        } else {
          this.uiElementsService.addSnackBar('Must be logged in');
          this.router.navigate(['/authentication'], {
            queryParams: { ref: state.url, test: 'login guard' }
          });
          return false;
        }
      })
    );
  }

}
