import { Injectable } from '@angular/core';
import { AngularFireAuth} from '@angular/fire/auth';
import { AngularFirestore } from '@angular/fire/firestore';
import * as firebase from 'firebase';
import { UIElementsService } from '../../shared/services/uielements.service';
import { User } from '../../shared/models/user';
import {Router} from '@angular/router';
import { Observable } from 'rxjs';
import { Subject } from 'rxjs';
import {switchMap} from 'rxjs/operators';
import { of } from 'rxjs/internal/observable/of';
@Injectable({
  providedIn: 'root'
})
export class AuthService {
   currentUser: User;
  user$: Observable<User|null>;
  authSubject = new Subject<boolean>();
  constructor(private angularFireAuth: AngularFireAuth,
    private angularFireStore: AngularFirestore,
    private uiElementsService: UIElementsService,
    private router: Router) {
      this.setUser();
    }


     /**
   * update the current user when the authentication state changes
   */
  private setUser() {
    this.user$ = this.angularFireAuth.authState.pipe(
      switchMap(user => {
        if (user) {
          return this.getUser(user.uid);
        } else {
          return of(null);
        }
      })
    );
    this.user$.subscribe(usr => {
      this.currentUser = usr;
    });
  }
 /**
  * takes care of google login
  */
  onGoogleLogin(){
    this.angularFireAuth
    .signInWithPopup(new firebase.default.auth.GoogleAuthProvider)
    .then(async (res) => {
      this.uiElementsService.addSpinner();
        const user: User = {
          name: res.user.displayName,
          email: res.user.email,
          uid: res.user.uid
        }
        if(res.additionalUserInfo.isNewUser){
          try {
            const result = await this.addNewUser(user.uid, user);

          } catch (error) {
            this.uiElementsService.addSnackBar(error);
          }
        }
        this.uiElementsService.removeSpinner();
        this.uiElementsService.addSnackBar('Welcome ' + res.user.displayName);
       window.setTimeout(() => {
        this.router.navigate(['/profile']);
       }, 100)

    })
    .catch((err) => {

    })
  }

  /**
   * Add a new user to the 'Users' collection.
   * @param user User. The details of the user to be added to the database.
   */
async addNewUser(userId: string, user: User){
   return this.angularFireStore
    .collection('users')
    .doc(userId)
    .set(Object.assign({}, user))
  }

  /**
   * Log user out and redirect to authentication page.
   */
 async logOut(){
   await this.angularFireAuth.signOut()
    this.router.navigate(['/authentication']);
  }
  /**
   * Assign value to currentUser.
   * @param user User. The user being set
   */
  // setUser(user: User){
  //   this.currentUser = user;
  // }
  /**
   * Get a copy of the user assigned.
   */
  getUser(userId: string) {
    return this.angularFireStore
    .collection('users')
    .doc<User>(userId).valueChanges();
  }
  /**
   *  Update the user's profile details. (Note this will update the values of the auth, and not the collection 'Users')
   * @param data {displayName: sting, photoURL: string}. Update the users profile values
   */
async updateUserProfile(data: {displayName: string, photoURL: string}){
    const user = firebase.default.auth().currentUser
    return user.updateProfile({
      displayName: data.displayName,
      photoURL: data.photoURL
    });
  }
}
